.array-container {
    position: relative;
    left: 25px;
    right: 25px;
    top: 10px;
}

.array-bar {
    width: 4px;
    background-color: rgb(0, 255, 191);
    display: inline-block;
    margin: 0 1px; 
}

.NavbarItems {
    background: linear-gradient(90deg, rgb(110, 94, 254) 0%, rgba(73,63,252,1) 100%);
    height: 80px;
    display: flex;
    justify-content: left;
    align-items: center;
    font-size: 1.2rem;
}

.header-bottom {
    background: linear-gradient(90deg, rgb(110, 94, 254) 0%, rgba(73,63,252,1) 100%);
    height: 40px;
    display: flex;
    
    position: fixed; 
    bottom:0%;
    width:100%; 
}

.navbar-logo {
    color: #fff;
    justify-self: start;
    width: 30vw;
    margin-left: 5px;
    cursor: pointer;
}
.navbar-logo2 {
    color: #fff;
    justify-self: start;
    width: 5vw;
    margin-left: 5px;
    cursor: pointer;
}


.fa-react {
    margin-left: 0.2rem;
    font-size: 1.6rem;
}

.nav-menu {
    display: grid;
    
    list-style: none;
    text-align: center;
    width: 18vw;
    justify-content: end;
    
}

.nav-links {
    color: white;
    text-decoration: none;
    justify-content: left;
    width: 4vw;
    padding: 0.5rem 1rem;
}

.nav-links:hover {
    background-color: #6d76f7;
    border-radius: 4px;
    transition: all 0.2s ease-out;
}

.fa-bars {
    color: #fff;
}

.nav-links-mobile {
    display: none;
}

.menu-icon {
    display: none;
    width: 10vw;
}

@media screen and (max-width: 930px) {
    .NavbarItems {
        position: relative;
    }

    .nav-menu {
        display: flex;
        flex-direction: column;
        width:100%;
        height: 500px;
        position: absolute;
        top: 70px;
        left: -100%;
        opacity: 1;
        transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: #6668f4;
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links {
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
    }

    .nav-links:hover {
        background-color: #7577fa;
        border-radius: 0;
    }

    .navbar-logo {
        position: absolute;
        top: 0;
        left: 0;
        width: 30vw;
        transform: translate(25%, 50%);
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        color: #fff;
        font-size: 2rem;
    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: #4ad9e4;
        text-decoration: none;
        color: #fff;
        font-size: 1.5rem;
    }

    .nav-links-mobile:hover {
        background: #fff;
        color: #6568F4;
        transition: 250ms;
    }

    Button {
        display: none;
    }



}